import React, { useMemo } from "react";
import { H1 } from "../../components/Header";
import HomeProductCard from "../../components/ProductCard";
import {formatProduct} from "../../../graphql/products";
import "./styles.scss";

const Section = (props) => {
  const {productMetadata} = props;
  if (!productMetadata) {
    return null;
  }

  const { products } = productMetadata;
  if (products.length === 0) {
    return null;
  }

  return (
    <div className="ProductContainer">
      {products.map((node) => {
        return (
          <HomeProductCard key={node.id} node={node} />
        );
      })}
    </div>
  );
}

const CategoryPage = (props) => {
  const { categoryName, productTypeMapping, products, photos } = props;
  const normalizedProducts = useMemo(
    () => {
      const categories = {};
      products.forEach(product => {
        const { category } = product;
        if (productTypeMapping[category]) {
          if (!categories[category]) {
            categories[category] = [];
          }

          const formattedProduct = formatProduct(product, photos);
          categories[category].push(formattedProduct);
        }
      });

      const res = Object.keys(categories)
        .filter(key => categories[key].length > 0)
        .map(key => {

          return {
            type: key,
            products: categories[key],
            metadata: productTypeMapping[key],
          };
        });

      return res;
    },
    [productTypeMapping, products, photos]
  );

  return (
    <div className="CategoryPage">

      <H1>Shop for {categoryName}</H1>

      <Section 
        productMetadata={normalizedProducts[0]} 
      />
    </div>
  )
};

export default CategoryPage;