import {findPhotos} from "../layout/images/photoFinder";

export const formatProduct = (product, photos) => {
    const associatedPhotos = findPhotos(photos, product.coverPhotoId);
    return {
        ...product,
        coverPhoto: associatedPhotos[0],
        photos: associatedPhotos,
        dimensions: [
            product.length ? `Length: ${product.length}` : null,
            product.width ? `Width: ${product.width}` : null,
            product.height ? `Height: ${product.height}` : null,
        ].filter(a => a),
    }
}
