import React from "react";
import { H3 } from "../Header";
import { Link as GatsyLink } from "gatsby";
import AddToCartButton from "../AddToCartButton";

import { GatsbyImage, getImage } from "gatsby-plugin-image";

import "./styles.scss";

const PetitsAnimauxProductCardImg = (props) => {
    const {coverPhoto, name} = props;
    return (
        <div className="PetitsAnimauxCardImage">
            <GatsbyImage
                image={getImage(coverPhoto.file)}
                alt={name}
            />
        </div> 
    );
}

const HomeProductCard = (props) => {
    const { node } = props;
    const {id, price, priceId} = node;
    const name = node.name.trim();
    const url = ("/product/" + id).trim();

    return (
        <div className="PetitsAnimauxCardContainer">
            <div className="PetitsAnimauxCard">

                <GatsyLink className="PetitsAnimauxCardLink" to={url}>
                    <PetitsAnimauxProductCardImg coverPhoto={node.coverPhoto} name={name} />
                    <H3 className="PetitsAnimauxCardTitle">{name}</H3>
                </GatsyLink>
                
                <AddToCartButton 
                    price={price} 
                    priceId={priceId} 
                    id={id} 
                />
            </div>
        </div>
    );
}

export default HomeProductCard;