import { graphql } from "gatsby";

import React from "react";
import MainSectionWrapper from "../layout";
import CommonHead from "../layout/Head";
import CategoryPage from "../layout/pages/CategoryPage";
import JsonLd from "../layout/jsonLd";

const baseJsonLd = {
  "@context": "http://schema.org/",
  "@type": "Organization",
  "name": "CrochetNest",
  "logo": "https://crochetnest.com/icons/icon-512x512.png",
  "url": "https://crochetnest.com/",
  "description": "Discover the unique items that Crochet Nests creates. We sell the best crochet products.",
};

const PageTemplate = (props) => {
    const {
    allGoogleSpreadsheetPetitsAnimauxCategories,
    allGoogleSpreadsheetPetitsAnimauxProducts,
    allGooglePhotosPhoto,
  } = props.data;
  const productTypeMapping = React.useMemo(() => {
    const productTypeMapping = {};
    allGoogleSpreadsheetPetitsAnimauxCategories.nodes.forEach((node) => {
      productTypeMapping[node.category] = {
        title: node.title,
      };
    });

    return productTypeMapping;
  }, [allGoogleSpreadsheetPetitsAnimauxCategories]);

  const products = allGoogleSpreadsheetPetitsAnimauxProducts.nodes;
  const photos = React.useMemo(() => {
    return allGooglePhotosPhoto.nodes.filter(photo => photo.description);
  }, [allGooglePhotosPhoto]);

  return (
    <>
      <MainSectionWrapper>
        <CategoryPage {...props} 
            categoryName={props.pageContext.categoryName}
            productTypeMapping={productTypeMapping} 
            products={products} 
            photos={photos}
        />
      </MainSectionWrapper>

      <JsonLd data={baseJsonLd} />
    </>
  )
};

export function Head(props) {
    const name = props.pageContext.categoryName;
    const data = {
        page: {
            name, 
        }
    };

    return <CommonHead {...props} data={data} />;
}

export const query = graphql`
  query PetitsAnimaux($category: String!) {
    allGoogleSpreadsheetPetitsAnimauxProducts {
      nodes {
        name
        id
        price
        category
        coverPhotoId
        priceId
        width
        height
        length
      }
    }
    
    allGoogleSpreadsheetPetitsAnimauxCategories(filter: { category: {eq: $category }}) {
        nodes {
            id
            category
            title
        }
    }

    allGooglePhotosPhoto(filter: {album: {title: {eq: "P2F"} } }) {
      nodes {
        id
        file {
          childImageSharp {
            gatsbyImageData(
              width: 300 
            )
          }
        }
        description
      }
    }

  }
`;

export default PageTemplate;